@charset "UTF-8";

@font-face {
  font-family: 'Bariol';
  src: url("../font/Bariol_Regular.otf");
  src: url("../font/Bariol_Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Bariol';
  src: url("../font/Bariol_Regular_Italic.otf");
  src: url("../font/Bariol_Regular_Italic.otf") format("opentype");
  font-weight: normal;
  font-style: italic;
}

h2,
.h2,
.h2sub,
.profit__items .item,
.humans__items .item,
.portfolio__items .item,
.pricing__items .item .plan {
  font-family: 'Bariol', sans-serif;
}

@font-face {
  font-family: 'fontello';
  src: url("../font/fontello.eot?98817037");
  src: url("../font/fontello.eot?98817037#iefix") format("embedded-opentype"), url("../font/fontello.svg?98817037#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'fontello';
  src: url("data:application/octet-stream;base64,") format("woff"), url("data:application/octet-stream;base64,") format("truetype");
}

