[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-cog:before {
  content: '\e800';
}

/* '' */

.icon-twitter:before {
  content: '\f099';
}

/* '' */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

h2,
.h2 {
  margin: 20px 0;
  text-align: center;
  font-size: 2.5rem;
}

h2 {
  letter-spacing: 5px;
}

.h2sub {
  margin: 20px 0;
  text-align: center;
  font-size: 20px;
  letter-spacing: 1px;
}

.profit {
  padding: 15px 0;
  background: #232324;
}

.profit h2 {
  color: #fff;
}

.profit .h2sub {
  color: #eb4f4e;
}

.profit__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
}

.profit__items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 6 - 10px);
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
}

.profit__items .item__img {
  border-radius: 50%;
}

.profit__items .item__subtitle {
  margin: 3px 0;
  font-size: 1.5rem;
  color: #fff;
}

.profit__items .item__text {
  font-size: 16px;
  color: #d94948;
}

.humans {
  background: #6cc372;
  padding: 10px 0;
}

.humans h2 {
  color: #232324;
}

.humans .h2sub {
  color: #fff;
}

.humans__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.humans__items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 6 - 10px);
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  background: #fff;
}

.humans__items .item__img {
  margin: 10px auto;
  width: 90%;
}

.humans__items .item__subtitle {
  margin: 10px 0;
  font-size: 1.5em;
  color: #6cc372;
}

.humans__items .item__job {
  margin: auto 10px 10px 10px;
  font-size: 1em;
  color: #eb4f4e;
}

.humans__items .item__follow {
  position: relative;
  padding: 5px;
  border-radius: 5px;
  background: #6cc372;
  font-size: 1.5em;
  color: #fff;
  cursor: pointer;
}

.humans__items .item__follow i {
  position: absolute;
  left: 15px;
}

.portfolio {
  padding: 10px 0 210px;
  background: #f0f0f0;
}

.portfolio h2 {
  color: #616161;
}

.portfolio .h2sub {
  color: #6cc372;
}

.portfolio__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
}

.portfolio__items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 5 - 10px);
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
  font-size: 20px;
}

.portfolio__items .item__img {
  margin: 10px auto;
  width: 100%;
  border-radius: 6px;
}

.portfolio__items .item__txt {
  color: #232324;
  font-size: 20px;
}

hr {
  border: none;
  margin-top: 0;
  margin-bottom: 1px;
  position: relative;
  z-index: 2;
  color: #d8d8d8;
  background-color: #d8d8d8;
  height: 1px;
}

.pricing {
  margin-top: -160px;
}

.pricing .h2 {
  color: #616161;
}

.pricing .h2sub {
  color: #eb4f4e;
}

.pricing__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  align-items: flex-end;
  justify-content: center;
}

.pricing__items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 8 - 10px);
  margin-top: 10px;
  margin-bottom: 10px;
  overflow: hidden;
  text-align: center;
  border-radius: 10px;
}

.pricing__items .item:nth-child(even) .h2 {
  color: #eb4f4e;
}

.pricing__items .item:nth-child(even) .h2sub {
  color: #616161;
}

.pricing__items .item:nth-child(even) .price {
  background: #eb4f4e;
}

.pricing__items .item .title {
  padding: 0 30px;
  overflow: hidden;
  background: #fff;
}

.pricing__items .item .plan {
  font-size: 18px;
  color: #616161;
}

.pricing__items .item .plan__item {
  padding: 15px 0;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #d8d8d8;
  background: #f0f0f0;
}

.pricing__items .item .price {
  padding: 10px 50px;
  position: relative;
  text-align: left;
  background: #616161;
  color: #fff;
}

.pricing__items .item .price__cur {
  font-size: 1.5em;
  vertical-align: 0%;
}

.pricing__items .item .price__val {
  font-size: 3em;
  vertical-align: middle;
}

.pricing__items .item .price__per {
  font-size: 1.2em;
  margin-top: 10px;
  vertical-align: -50%;
}

.pricing__items .item .price__btn {
  position: absolute;
  right: 50px;
  top: 25px;
  padding: 5px 20px;
  border-radius: 2px;
  color: #232324;
  text-decoration: none;
  background: #fff;
}

footer .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

footer .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 8 - 10px);
  min-height: 200px;
  background: #aaa;
}

footer .items .item .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

footer .items .item .row div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 24 - 10px);
  margin-bottom: 5px;
  adding: 5px;
  background: #f50;
}

@media screen and (max-width: 960px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .profit__items .item {
    width: calc(100% / 24 * 12 - 10px);
  }

  .portfolio__items .item {
    width: calc(100% / 24 * 6 - 10px);
  }

  .pricing {
    margin-top: -149px;
  }

  .pricing .h2 {
    font-size: 1.8rem;
  }

  .pricing__items .item .price {
    padding: 10px 15px;
  }

  .pricing__items .item .price__btn {
    right: 15px;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .profit__items .item {
    width: calc(100% / 24 * 12 - 10px);
  }

  .humans h2 {
    font-size: 2rem;
  }

  .humans__items .item {
    width: calc(100% / 24 * 12 - 10px);
  }

  .portfolio {
    padding-bottom: 170px;
  }

  .portfolio__items .item {
    width: calc(100% / 24 * 12 - 10px);
  }

  .pricing {
    margin-top: -120px;
  }

  .pricing .h2 {
    font-size: 1.5rem;
  }

  .pricing__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .pricing__items .item:nth-child(n+2) .title {
    border: 1px solid #d8d8d8;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .pricing__items .item .price {
    padding: 10px 100px;
  }

  .pricing__items .item .price__btn {
    right: 100px;
  }

  footer .items .item {
    width: calc(100% / 24 * 24 - 10px);
  }

  footer .items .item .row div {
    width: calc(100% / 24 * 8 - 10px);
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .profit__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .humans__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .humans__items .item {
    display: block;
    font-size: 20px;
    background: #eee;
  }

  .portfolio__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .pricing__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .pricing__items .item .price {
    padding: 10px 50px;
  }

  .pricing__items .item .price__btn {
    right: 50px;
  }

  footer .items .item .row div {
    width: calc(100% / 24 * 24 - 10px);
  }
}

@media screen and (max-width: 420px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .humans__items .item {
    width: calc(100% / 24 * 24 - 10px);
  }

  .portfolio__items .item {
    width: calc(100% / 24 * 24 - 10px);
  }

  .pricing__items .item {
    width: calc(100% / 24 * 20 - 10px);
  }

  .pricing__items .item .price {
    padding: 10px 15px;
  }

  .pricing__items .item .price__btn {
    right: 15px;
  }
}